<template>
  <div class="popover-container">
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      :append-to-body="true"
      width="30%"
      center
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ["dialogDeleteSwitch", "diglogDeleteData"],
  data() {
    return {
      centerDialogVisible: false,
    };
  },
  watch: {
    // dialogDeleteSwitchtrueDialog
    dialogDeleteSwitch() {
      if (this.dialogDeleteSwitch) {
        this.centerDialogVisible = true;
      } else {
        this.centerDialogVisible = false;
      }
    },
    // DialogdialogDeleteSwitchfalse
    centerDialogVisible() {
      if (!this.centerDialogVisible) {
        this.$emit("hide");
      }
    },
  },
  methods: {
    handleSubmit() {
      this.$emit("hide");
      // 
      this.$axios(`/products/${this.diglogDeleteData.id}`, {
        method: "delete",
        data: {
          appkey: "Mr_Xu_Peng_Hui_1612743894720",
        },
      }).then((resp) => {
        if (resp.status === "success") {
          this.$message({
            message: "",
            type: "success",
          });
          this.$emit("get-table-data");
        } else {
          this.$message({
            message: "",
            type: "warning",
          });
        }
      });
    },
  },
};
</script>

