<template>
  <div class="login-container">
    <el-form
      :model="login_user"
      status-icon
      :rules="rules"
      ref="login_user"
      class="demo-ruleForm form-container"
    >
      <el-row>
        <el-col :span="24">
          <el-form-item class="input-size" prop="username">
            <el-input
              placeholder=""
              v-model="login_user.username"
              clearable
            >
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item class="input-size" prop="password">
            <el-input
              placeholder=""
              v-model="login_user.password"
              type="password"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row class="btn-container">
        <el-col :span="12">
          <el-button
            type="success"
            icon="el-icon-s-promotion"
            @click="submitForm('login_user')"
            ></el-button
          >
        </el-col>
        <el-col :span="12">
          <el-button type="primary" @click="handleToRegister"></el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const checkEmail = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        const regEmail = new RegExp(
          '^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(.[a-zA-Z0-9_-]+)+$',
        );
        const isEmail = regEmail.test(value);
        if (!isEmail) {
          callback(new Error(''));
        }
        callback();
      }
    };
    return {
      login_user: {
        username: 'xupenghui163@163.com',
        password: '123456',
      },
      rules: {
        password: [{ validator: validatePass, trigger: 'blur' }],
        username: [{ validator: checkEmail, trigger: 'blur' }],
      },
    };
  },
  methods: {
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios('/passport/login', {
            method: 'post',
            data: {
              email: this.login_user.username,
              password: this.login_user.password,
            },
          }).then((resp) => {
            if (resp.status === 'success') {
              // cookie
              this.$store.dispatch('setUserCookie', resp.data);
              setTimeout(() => {
                this.$message({
                  showClose: true,
                  message: resp.msg,
                  type: 'success',
                });
              }, 500);
              setTimeout(() => {
                // true
                const noIndex = this.$router.matcher.getRoutes().every((element) => element.name !== 'Index');
                if (noIndex) {
                  this.$router.push('/index');
                }
                this.$router.push('/index');
              }, 1000);
            }
          });
        } else {
          return false;
        }
        return true;
      });
    },
    handleToRegister() {
      this.$router.push('/register');
    },
  },
};
</script>

